.navbar {
  position: sticky;
  top: 0;
  background-color: white;
  width: 100%;
  height: 90px;
  padding-block: 0.5rem;
  padding-inline: 0;
  display: flex;
  z-index: 1;
}

.navbar-container {
  width: 100%;
  max-width: 1280px;
  display: inline-flex;
  align-items: center;
  margin-inline: auto;
}

.navbar-logo {
  width: 180px;
  flex-grow: 1;
}

.navbar-logo-container {
  flex-grow: 1;
  padding-block: 0.3125rem;
}

.navbar-nav {
  display: flex;
  list-style: none;
}

.nav-item {
  padding-inline: 1rem;
}

.nav-link {
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  transition: text-decoration 0.3s ease;
}

.nav-link:hover {
  text-decoration: none;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid;
}

/* MenuButton.css */
.menu-button {
  background: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 2px;
  align-items: center;
  height: 40px;
  width: 50px;
  margin-inline: 8px;
}

.bar {
  display: flex;
  width: 28px;
  height: 8px;
  background-color: #5c81eb;
}

.middle,
.down {
  margin-top: 5px;
}

.middle {
  width: 32px;
}
/* SideMenu.css */
.side-menu {
  position: fixed;
  top: 0;
  left: -250px; /* Inicialmente oculto fuera del área visible */
  width: 250px;
  height: 100%;
  background-color: #333;
  transition: left 0.3s ease;
  overflow-x: hidden;
  padding-top: 60px; /* Ajusta según tus necesidades */
}

.side-menu.open {
  left: 0;
}

.side-menu ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.side-menu ul li {
  padding-block: 1rem;
}
.mobile-item {
  padding: 15px;
  color: white;
  cursor: pointer;
  text-decoration: none;
}
